<template>
	<view class="content">
		<view class="cu-form-group">
			<view class="title">安装师傅昵称</view>
			<input maxlength="30" class="text-right" placeholder="昵称/姓名" v-model="details.name" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group">
			<view class="title">手机号</view>
			<input maxlength="11" class="text-right" type="number" placeholder="手机号" v-model="details.mobile" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group">
			<view class="title">安装费</view>
			<input maxlength="11" class="text-right" type="number" placeholder="运费" v-model="details.install_fee" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group align-start">
			<view class="title">备注</view>
			<textarea maxlength="200" v-model="details.remark" placeholder="如安装时间"></textarea>
		</view>
		<view class="cu-list menu sm-border margin-top">
			<view class="cu-item arrow" @click="goodsAllocated()">
				<view class="content">
					<text>分配商品</text>
				</view>
			</view>
		</view>
		<view class="goods-section margin-top-sm padding-top-sm" v-for="(item, index) in details.good_indent_commodity" :key="index" @click="selectGoods(item)">
			<view class="g-item">
				<image :src="item.img"></image>
				<view class="right">
					<text class="title clamp">{{item.name}}</text>
					<text v-if="item.good_sku_id" class="spec">{{item.specification}}</text>
					<view class="price-box">
						<text class="price text-red text-price">{{item.price}}</text>
						<button class="cu-btn round text-right delete-button" @click.stop="deleteCartItem(index)">删除</button>
					</view>
				</view>
			</view>
		</view>
		<button class="add-btn" @click="confirm">提交</button>
	</view>
</template>

<script>
	import Install from '../../api/install'
	import { objectURLToBlob } from '../../utils'
	export default {
		data() {
			return {
				id: '',
				oid: '',
				details: {
					good_indent_id: 0,
					name: '',
					mobile: '',
					install_fee: '',
					remark: '',
					good_indent_commodity: []
				}
			}
		},
		onLoad(option){
			this.oid = option.oid
			this.details.good_indent_id = this.oid
			this.id = option.id
			this.index = option.index
			this.getList()
		},
		methods: {
			async getList(){
				let title = '新增安装配置'
				const that = this
				if(this.id > 0){
					title = '编辑安装配置'
					Install.getDetails(this.id,{},function(res){
						that.details = res
					})
				}
				uni.setNavigationBarTitle({
					title
				})
			},
			
			//提交
			confirm(){
				let data = this.details
				const that = this
				if(!data.name){
					this.$api.msg('请填写配送师傅的昵称');
					return;
				}
				if(!data.mobile){
					this.$api.msg('请填写手机号');
					return;
				}
				if(!(/(^[1-9]\d*$)/.test(data.mobile))){
					this.$api.msg('手机号格式有误');
					return;
				}
				if(!data.install_fee){
					this.$api.msg('请填写安装费');
					return;
				}
				if(!(/(^[1-9]\d*$)/.test(data.install_fee))){
					this.$api.msg('安装费只能是正整数');
					return;
				}
				
				if(data.id){
					Install.updateSubmit(data,function(res){
						that.$api.prePage().refreshInstallList(that.index, data)
						that.$api.msg(`配送配置修改成功`);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}else{
					Install.createSubmit(data,function(res){
						data.id = res
						that.$api.prePage().refreshInstallList(that.index, data)
						that.$api.msg(`配送配置添加成功`);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}
			},
			// 分配商品回调
			refreshGoodsAllocated(data){
				this.details.good_indent_commodity = []
				if (data.length > 0) {
					data.forEach(item=>{
						if(item.checked){
							this.details.good_indent_commodity.push(item)
						}
					})
				}
			},
			//删除分配的商品
			deleteCartItem(index){
				this.details.good_indent_commodity.splice(index, 1)
			},
			goodsAllocated(){
				let good = []
				this.details.good_indent_commodity.forEach(item=>{
					good.push(item.id)
				})
				uni.navigateTo({
					url: `/pages/order/goods_allocated?oid=${this.oid}&good=${good}`
				})
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}

	.row{
		display: flex;
		align-items: center;
		position: relative;
		padding:0 30upx;
		height: 110upx;
		background: #fff;
		
		.tit{
			flex-shrink: 0;
			width: 120upx;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.input{
			flex: 1;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.icon-shouhuodizhi{
			font-size: 36upx;
			color: $font-color-light;
		}
	}
	.default-row{
		margin-top: 16upx;
		.tit{
			flex: 1;
		}
		switch{
			transform: translateX(16upx) scale(.9);
		}
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
	.goods-section {
		background: #fff;
		padding-bottom: 1px;
	
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}
	
		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}
	
		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}
	
		.g-item {
			display: flex;
			margin: 20upx 30upx;
	
			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}
	
			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}
	
			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}
	
			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}
	
			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
	
				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
				.delete-button{
					position:absolute;
					right:20upx;
				}
			}
			.text-icon{
				font-size: 40upx;
			}
			.step-box {
				position: relative;
			}
		}
	}
</style>
